import PropTypes from 'prop-types';
// import { useState } from 'react';
// import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
// import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';

function CollageImageCount({ count }) {
  return (
    <Stack direction="row" alignItems="center" justifyContent="center">
      <Typography
        sx={{
          color: 'form.input',
          fontSize: '0.87rem',
          fontWeight: 'bold',
          px: 1,
        }}
      >
        Images:
      </Typography>
      <Typography
        sx={{
          color: 'primary.main',
          fontSize: '0.87rem',
          fontWeight: 'bold',
          p: 0,
        }}
      >
        {count}
      </Typography>
    </Stack>
  );
}

CollageImageCount.propTypes = {
  count: PropTypes.number.isRequired,
};

function CollageType({ type, handleChange, sizes }) {
  return (
    <Stack direction="row" alignItems="center" justifyContent="center">
      <Typography
        sx={{
          color: 'form.input',
          fontSize: '0.87rem',
          fontWeight: 'bold',
        }}
      >
        Type:{' '}
      </Typography>
      <FormControl
        variant="standard"
        size="small"
        margin="none"
        hiddenLabel
        sx={{ alignItems: 'center' }}
      >
        <Select
          id="demo-simple-select-standard"
          value={type}
          onChange={handleChange}
          label="Age"
          disableUnderline
          inputProps={{
            sx: {
              p: 0,
              fontWeight: 'bold',
              fontSize: '0.82rem',
              color: 'primary.main',
              pl: 1,
            },
          }}
        >
          {Object.keys(sizes).map((key) => (
            <MenuItem
              divider
              key={sizes[key].text}
              value={key}
              sx={{
                alignItems: 'center',
                p: 0,
                px: 1,
                minHeight: '30px',
                fontSize: '0.87rem',
                fontWeight: 'bold',
                color: 'gray',
              }}
            >
              {sizes[key].icon}
              {sizes[key].text}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Stack>
  );
}

CollageType.propTypes = {
  type: PropTypes.string.isRequired,
  sizes: PropTypes.objectOf(
    PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      icon: PropTypes.node,
    })
  ).isRequired,
  handleChange: PropTypes.func.isRequired,
};

export default function CollageMetaInfo({
  collageMetaRef,
  collageType,
  width,
  sizes,
  handleTypeChange,
  imageCount,
}) {
  //
  // const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  const handleChange = (event) => {
    const ct = event.target.value;
    handleTypeChange(ct);
  };

  return (
    <Stack
      ref={collageMetaRef}
      direction="row"
      alignItems="center"
      justifyContent="space-around"
      sx={{
        width,
        textAlign: 'middle',
        px: 1,
      }}
    >
      <CollageType
        type={collageType}
        sizes={sizes}
        handleChange={handleChange}
      />
      <CollageImageCount count={imageCount} />
    </Stack>
  );
}

CollageMetaInfo.propTypes = {
  collageType: PropTypes.string.isRequired,
  width: PropTypes.number.isRequired,
  sizes: PropTypes.objectOf(
    PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      icon: PropTypes.node,
    })
  ).isRequired,
  handleTypeChange: PropTypes.func.isRequired,
  imageCount: PropTypes.number.isRequired,
  collageMetaRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
};
